import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getCTAContainerGridConfig } from '../../configs/grid-configs';
import { AdStatus, GameOverReasons } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { CTAEmbeddedView } from './cta-embedded-view';
import { CTAStandardView } from './cta-standard-view';

/* eslint-disable class-methods-use-this */
export class CTAContainer extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
  }

  getGridConfig() {
    return getCTAContainerGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Cta:
        this._build();
        break;
      case AdStatus.Game:
        if (this._cta) {
          this._ctaHideTw = this._cta.hide();
          this._ctaHideTw.onComplete.add(() => {
            this._cta.destroy();
            this._cta = null;
          });
        }
        break;
      default:
    }
  }

  _build() {
    this._cta && this._destroyCta();

    this.build(this.getGridConfig());
    this._buildCta();
  }

  _destroyCta() {
    this.game.tweens.remove(this._ctaHideTw);
    this._cta.destroy();
    this._cta = null;
  }

  _buildCta() {
    const { cta_type: type } = CI_API.Globals.PARAMS;
    const { reason } = store.ad.cta;

    switch (type) {
      case 'standard':
        this._buildStandardCta();
        break;
      case 'embedded':
        reason === GameOverReasons.ItemsToCtaReached ? this._buildEmbeddedCta() : this._buildStandardCta();
        break;
      default:
        break;
    }
  }

  _buildStandardCta() {
    this._cta = new CTAStandardView();
    this.setChild('cta', this._cta);
  }

  _buildEmbeddedCta() {
    this._cta = new CTAEmbeddedView();
    this.setChild('cta', this._cta);
  }
}
