/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { AdStatus, DotState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { difference } from '../../utils/array/difference';
import { Container } from '../../utils/container';
import { DotComponent } from './dot-component';
import { PointerComponent } from './pointer-component';

export class ProgressBarView extends Container {
  constructor() {
    super();

    this._dots = [];

    lego.event
      .on(ModelEvents.ProgressBarModel.DotsUpdate, this._onDotsUpdate, this)
      .on(ModelEvents.DotModel.StateUpdate, this._onDotsStateUpdate, this)
      .on(ModelEvents.ProgressBarModel.ProgressUpdate, this._onProgressUpdate, this);

    this._build();
  }

  getBounds() {
    return new Phaser.Rectangle(...LP([-13.5, -250, 27, 500], [-250, -13.5, 500, 27]));
  }

  rebuild() {
    store.ad.status !== AdStatus.Cta && this._updateProgress(store.game.progressBarModel.progress);
    this._pointer && this._pointer.rebuild();
  }

  _onDotsUpdate(dots) {
    this._buildDots(dots);
  }

  _onDotsStateUpdate(state, previousValue, uuid) {
    const dot = this._getDotByUuid(uuid);

    switch (state) {
      case DotState.Idle:
        dot.idle();
        break;
      case DotState.Glowing:
        dot.glow();
        break;
      case DotState.Filled:
        dot.fill();
        break;
      default:
        break;
    }
  }

  _onProgressUpdate(progress) {
    this._updateProgress(progress);
  }

  _updateProgress(progress) {
    this._updatePointer(progress);
    this._updateGrWidth(this._maskRect, progress);
  }

  _getDotByUuid(uuid) {
    return this._dots.find(d => d.uuid === uuid);
  }

  _updateGrWidth(gr, progress) {
    const { x, y, width, height } = this._fill;
    gr.clear();
    gr.beginFill(0xff0000, 0.6);
    gr.drawRect(x - width / 2, y - height / 2, width * progress, height);
    gr.endFill();
  }

  _updatePointer(progress) {
    this._pointer.updateText(progress * 100);
    this._pointer.position.set(progress * this._bg.width - this._bg.width / 2, -10);
  }

  _build() {
    this._buildBg();
    this._buildFill();
    this._buildMask();
    this._buildPointerComponent();

    this._buildBounds();
  }

  _buildBounds() {
    const { x, y, width, height } = this.getBounds();
    const gr = this.game.add.graphics();
    gr.beginFill(0xff0000, 0);
    gr.drawRect(x, y, width, height);
    gr.endFill();
    this.addChild(gr);
  }

  _buildBg() {
    const gr = this.game.add.graphics();
    gr.beginFill(0x9396ff, 1);
    gr.drawRoundedRect(-250, -10, 500, 20, 20);
    gr.endFill();

    this.addChild((this._bg = gr));
  }

  _buildFill() {
    const gr = this.game.add.graphics();
    gr.beginFill(0xfef955, 1);
    gr.drawRoundedRect(-250, -10, 500, 20, 20);
    gr.endFill();

    this.addChild((this._fill = gr));
  }

  _buildMask() {
    const gr = this.game.add.graphics();
    gr.beginFill(0x00ff00);
    gr.drawRect(0, 0, 0, 0);
    this.addChild((this._maskRect = gr));

    this._fill.mask = gr;
  }

  _buildPointerComponent() {
    this._pointer = new PointerComponent();
    this._pointer.position.set(this._bg.left, -10);

    this.addChild(this._pointer);
  }

  _buildDots(dots) {
    const dotsModels = dots.map(d => d.uuid);
    const dotsView = this._dots.map(d => d.uuid);

    const newDots = difference(dotsModels, dotsView);

    newDots.forEach(uuid => {
      const dot = new DotComponent(uuid);

      dot.position.set(this._dots.length * (this._bg.width / 2) - this._bg.width / 2, 0);

      this._dots.push(dot);

      this.addChild(dot);
    });
  }
}
