import { SoundPartState } from '../../constants';
import { store } from '../../models/store';

export function checkForRightPadClickedCommand(sound, time) {
  store.game.boardModel.playSequence.forEach(sequence => {
    const { name, startTime, state } = sequence;
    const offset = 300;

    if (sound === name && state !== SoundPartState.Play && time > startTime - offset && time < startTime + offset) {
      sequence.state = SoundPartState.Play;
      store.player.score += 1;
    }
  });
}
