import { getAutoPlaySoundPartsConfig, getPadsConfig, getPlaySequence } from '../../../configs/board-config';
import { BoardState } from '../../../constants';
import { delayRunnable, removeRunnable } from '../../../utils';
import { ObservableModel } from '../../observable-model';
import { PadModel } from './pad-model';
import { SoundPartModel } from './sound-part-model';

export class BoardModel extends ObservableModel {
  constructor() {
    super('BoardModel');

    this._pads = [];
    this._playSequence = [];
    this._autoPlaySounds = [];
    this._simulationSequence = [];
    this._sequence = null;
    this._state = BoardState.Unknown;

    this.makeObservable();
  }

  get pads() {
    return this._pads;
  }

  get playSequence() {
    return this._playSequence;
  }

  get autoPlaySounds() {
    return this._autoPlaySounds;
  }

  get simulationSequence() {
    return this._simulationSequence;
  }

  get sequence() {
    return this._sequence;
  }

  set sequence(value) {
    this._sequence = value;
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  destroy() {
    removeRunnable(this._simulateRunnable);
    this._simulateRunnable = null;

    super.destroy();
  }

  getPadByUuid(uuid) {
    return this._pads.find(p => p.uuid === uuid);
  }

  getPadBySound(sound) {
    return this._pads.find(p => p.sound === sound);
  }

  getPadByPosition(row, column) {
    return this._pads.find(p => p.row === row && p.column === column);
  }

  getAutoPlaySoundByUuid(uuid) {
    return this._autoPlaySounds.find(p => p.uuid === uuid);
  }

  getSimulationSoundByUuid(uuid) {
    return this._simulationSequence.find(p => p.uuid === uuid);
  }

  getSimulationSoundByState(state) {
    return this._simulationSequence.find(p => p.state === state);
  }

  simulate() {
    const delay = 500;

    this._simulateRunnable = delayRunnable(delay, () => {
      this._state = BoardState.Simulation;
    });
  }

  resetAutoPlaySounds() {
    this._autoPlaySounds = [];

    this._initializeAutoPlaySounds();
  }

  resetPlaySequence() {
    this._playSequence = [];

    this._initializePlaySequence();
  }

  resetSimulationSequence() {
    this._simulationSequence = [];

    this._initializeSimulationSequence();
  }

  addPad(config) {
    const { row, column, color, sound } = config;
    const pad = new PadModel(row, column, color, sound);

    this._pads = [...this._pads, pad];

    pad.initialize();
  }

  initialize() {
    this._sequence = 0;

    this._initializePads();
    this._initializeAutoPlaySounds();
    this._initializePlaySequence();
    this._initializeSimulationSequence();

    this._state = BoardState.Idle;
  }

  _initializePads() {
    const padConfig = getPadsConfig();

    for (let i = 0; i < padConfig.length; i += 1) {
      const row = padConfig[i];
      for (let j = 0; j < row.length; j += 1) {
        this.addPad(row[j]);
      }
    }
  }

  _initializeAutoPlaySounds() {
    const { tutorial } = CI_API.Globals.PARAMS;
    const soundPartsConfig = getAutoPlaySoundPartsConfig();
    const arr = [];

    for (let i = 0; i < soundPartsConfig[this._sequence].length; i += 1) {
      const { name, startTime } = soundPartsConfig[this._sequence][i];
      const part = new SoundPartModel(name, startTime);

      arr.push(part);
    }

    let a = [];
    soundPartsConfig.forEach(s => {
      const b = [];
      for (let i = 0; i < s.length; i += 1) {
        const { name, startTime } = s[i];
        const part = new SoundPartModel(name, startTime);
        part.initialize();

        b.push(part);
      }
      a = [...a, ...b];
    });

    tutorial ? (this._autoPlaySounds = [...arr]) : (this._autoPlaySounds = [...a]);
  }

  _initializePlaySequence() {
    const { tutorial } = CI_API.Globals.PARAMS;
    const sequences = getPlaySequence();
    const arr = [];

    for (let i = 0; i < sequences[this._sequence].length; i += 1) {
      const { name, startTime } = sequences[this._sequence][i];
      const part = new SoundPartModel(name, startTime);

      arr.push(part);
    }

    let a = [];
    sequences.forEach(s => {
      const b = [];
      for (let i = 0; i < s.length; i += 1) {
        const { name, startTime } = s[i];
        const part = new SoundPartModel(name, startTime);
        part.initialize();

        b.push(part);
      }
      a = [...a, ...b];
    });

    tutorial ? (this._playSequence = [...arr]) : (this._playSequence = [...a]);
  }

  _initializeSimulationSequence() {
    const { tutorial } = CI_API.Globals.PARAMS;
    const sequences = getPlaySequence();
    const arr = [];

    for (let i = 0; i < sequences[this._sequence].length; i += 1) {
      const { name, startTime } = sequences[this._sequence][i];
      const part = new SoundPartModel(name, startTime);
      part.initialize();

      arr.push(part);
    }

    let a = [];
    sequences.forEach(s => {
      const b = [];
      for (let i = 0; i < s.length; i += 1) {
        const { name, startTime } = s[i];
        const part = new SoundPartModel(name, startTime);
        part.initialize();

        b.push(part);
      }
      a = [...a, ...b];
    });

    tutorial ? (this._simulationSequence = [...arr]) : (this._simulationSequence = [...a]);
  }
}
