export function getOpeningPads() {
  let pads = [];

  switch (CI_API.Globals.PARAMS.difficulty) {
    case 'easy':
      pads = [
        {
          row: 3,
          column: 0
        },
        {
          row: 3,
          column: 1
        },
        {
          row: 3,
          column: 2
        }
      ];
      break;
    case 'medium':
      pads = [
        {
          row: 2,
          column: 2
        },
        {
          row: 3,
          column: 0
        },
        {
          row: 3,
          column: 1
        },
        {
          row: 3,
          column: 2
        }
      ];
      break;
    case 'hard':
      pads = [
        {
          row: 2,
          column: 1
        },
        {
          row: 2,
          column: 2
        },
        {
          row: 3,
          column: 0
        },
        {
          row: 3,
          column: 1
        },
        {
          row: 3,
          column: 2
        }
      ];
      break;
    default:
      break;
  }

  return pads;
}

export function getAutoPlaySoundPartsConfig() {
  let parts = [];

  switch (CI_API.Globals.PARAMS.difficulty) {
    case 'easy':
      parts = [
        [{ name: 'first_sequence', startTime: 0 }],
        [
          { name: 'missing_part_1', startTime: 4000 },
          { name: 'missing_part_2', startTime: 7000 },
          { name: 'second_sequence', startTime: 7400 }
        ]
      ];
      break;
    case 'medium':
      parts = [
        [
          { name: 'first_sequence', startTime: 0 },
          { name: 'missing_part_1', startTime: 4000 }
        ],
        [{ name: 'second_sequence', startTime: 7400 }]
      ];
      break;
    case 'hard':
      parts = [[{ name: 'first_sequence', startTime: 0 }], [{ name: 'second_sequence', startTime: 7400 }]];
      break;
    default:
      break;
  }

  return parts;
}

