export function getSequenceStartEndTime(sequence) {
  const { difficulty } = CI_API.Globals.PARAMS;
  let time;

  switch (sequence) {
    case 0:
      if (difficulty === 'easy') {
        time = { startTime: 0, endTime: 4000 };
      } else if (difficulty === 'medium') {
        time = { startTime: 0, endTime: 7000 };
      } else if (difficulty === 'hard') {
        time = { startTime: 0, endTime: 7400 };
      }
      break;
    case 1:
      if (difficulty === 'easy') {
        time = { startTime: 4000, endTime: 19000 };
      } else if (difficulty === 'medium') {
        time = { startTime: 7000, endTime: 19000 };
      } else if (difficulty === 'hard') {
        time = { startTime: 7400, endTime: 19000 };
      }
      break;
    default:
      break;
  }

  return time;
}
