import { CellAlign } from '@armathai/phaser2-grid';
import { isNarrowScreen } from '../../utils';

export function getUIGridLandscapeConfig() {
  return {
    // debug: { color: 0x4287f5 },
    name: 'ui',
    cells: [
      {
        name: 'p_cta',
        padding: 0.05,
        bounds: { x: 0, y: 0.6, width: 0.3, height: 0.175 }
      }
    ]
  };
}

export function getUIGridPortraitConfig() {
  return {
    // debug: { color: 0x4287f5 },
    name: 'ui',
    cells: [
      {
        name: 'p_cta',
        align: isNarrowScreen() ? CellAlign.CenterTop : CellAlign.Center,
        bounds: { x: 0, y: 0.85, width: 1, height: 0.155 }
      }
    ]
  };
}
