/* eslint-disable class-methods-use-this */
import { Container } from '../../utils/container';
import { DotComponent } from '../game/dot-component';

export class CTAProgressComponent extends Container {
  constructor(progress) {
    super();

    this._dots = [];
    this._build();
    this._updateProgress(progress);
  }

  _updateProgress(progress) {
    this._updateGrWidth(this._maskRect, progress);
    if (progress === 1) {
      this._dots[0].fill();
      this._dots[1].fill();
      this._dots[2].fill();
    } else if (progress >= 0.5) {
      this._dots[0].fill();
      this._dots[1].fill();
    } else if (progress < 0.5) {
      this._dots[0].fill();
    }
  }

  _updateGrWidth(gr, progress) {
    const { x, y, width, height } = this._fill;
    gr.clear();
    gr.beginFill(0xff0000, 0.6);
    gr.drawRect(x - width / 2, y - height / 2, width * progress, height);
    gr.endFill();
  }

  _build() {
    this._buildBg();
    this._buildFill();
    this._buildMask();
    this._buildDots();

    this._buildBounds();
  }

  _buildBounds() {
    const { x, y, width, height } = this.getBounds();
    const gr = this.game.add.graphics();
    gr.beginFill(0xff0000, 0);
    gr.drawRect(x, y, width, height);
    gr.endFill();
    this.addChild(gr);
  }

  _buildBg() {
    const gr = this.game.add.graphics();
    gr.beginFill(0x9396ff, 1);
    gr.drawRoundedRect(-250, -10, 500, 20, 20);
    gr.endFill();

    this.addChild((this._bg = gr));
  }

  _buildFill() {
    const gr = this.game.add.graphics();
    gr.beginFill(0xfef955, 1);
    gr.drawRoundedRect(-250, -10, 500, 20, 20);
    gr.endFill();

    this.addChild((this._fill = gr));
  }

  _buildMask() {
    const gr = this.game.add.graphics();
    gr.beginFill(0x00ff00);
    gr.drawRect(0, 0, 0, 0);
    this.addChild((this._maskRect = gr));

    this._fill.mask = gr;
  }

  _buildDots() {
    const count = 3;
    for (let i = 0; i < count; i += 1) {
      const dot = new DotComponent();
      dot.idle();
      dot.position.set(this._dots.length * (this._bg.width / 2) - this._bg.width / 2, 0);

      this._dots.push(dot);

      this.addChild(dot);
    }
  }
}
