/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getGameGridConfig } from '../../configs/grid-configs';
import { ModelEvents } from '../../events/model-events';
import { BoardView } from './board/board-view';
import { ProgressBarView } from './progress-bar-view';
import { TimerView } from './timer-view';

export class GameView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    this._boardView = null;

    lego.event
      .on(ModelEvents.GameModel.BoardModelUpdate, this._onBoardUpdate, this)
      .on(ModelEvents.GameModel.ProgressBarModelUpdate, this._onProgressBarUpdate, this);
  }

  get name() {
    return 'GameView';
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    this._boardView && this._rebuildBoard();
    this._progressBarView && this._rebuildProgressBar();

    super.rebuild(config);
  }

  _rebuildBoard() {
    this._boardView.rebuild();
    this._boardView.angle = LP(90, 0);
  }

  _rebuildProgressBar() {
    this._progressBarView.rebuild();
    this._progressBarView.angle = LP(-90, 0);
  }

  getGridConfig() {
    return getGameGridConfig();
  }

  // BOARD
  _onBoardUpdate(board) {
    board && this._buildBoard();
  }

  _buildBoard() {
    this._boardView && this._destroyBoard();

    this._boardView = new BoardView();
    this._boardView.angle = LP(90, 0);
    this.setChild('board', this._boardView);
  }

  _destroyBoard() {
    this._boardView.destroy();
    this._boardView = null;
  }

  // PROGRESS BAR
  _onProgressBarUpdate(progressBar) {
    progressBar && this._buildProgressBar();
  }

  _buildProgressBar() {
    this._progressBarView && this._destroyProgressBar();
    this._progressBarView = new ProgressBarView();
    this._progressBarView.angle = LP(-90, 0);
    this.setChild('progress_bar', this._progressBarView);
  }

  _destroyProgressBar() {
    this._progressBarView.destroy();
    this._progressBarView = null;
  }

  // TIMER
  _onTimerUpdate(timer) {
    timer ? this._buildTimer() : this._destroyTimer();
  }

  _buildTimer() {
    this._timerView = new TimerView();
    this.setChild('timer', this._timerView);
  }

  _destroyTimer() {
    this._timerView.destroy();
    this._timerView = null;
  }
}
