import { PadState } from '../../../constants';
import { ObservableModel } from '../../observable-model';

export class PadModel extends ObservableModel {
  constructor(row, column, color, sound) {
    super('PadModel');

    this._row = row;
    this._column = column;
    this._color = color;
    this._sound = sound;
    this._state = PadState.Unknown;

    this.makeObservable();
  }

  get row() {
    return this._row;
  }

  get column() {
    return this._column;
  }

  get color() {
    return this._color;
  }

  get sound() {
    return this._sound;
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  idle() {
    this._state = PadState.Idle;
  }

  block() {
    this._state = PadState.Blocked;
  }

  enable() {
    this._state = PadState.Enabled;
  }

  disable() {
    this._state = PadState.Disabled;
  }

  initialize() {
    this._state = PadState.Blocked;
  }
}
