import { DotState } from '../../constants';
import { ObservableModel } from '../observable-model';
import { DotModel } from './dot-model';

export class ProgressBarModel extends ObservableModel {
  constructor() {
    super('ProgressBarModel');

    this._dots = [];
    this._progress = null;

    this.makeObservable();
  }

  get dots() {
    return this._dots;
  }

  get progress() {
    return this._progress;
  }

  set progress(value) {
    this._progress = value;
  }

  getDotByUuid(uuid) {
    return this._dots.find(d => d.uuid === uuid);
  }

  getDotAtPosition(index) {
    return this._dots[index];
  }

  reset() {
    this._dots.forEach((d, i) => {
      i === 0 ? (d.state = DotState.Filled) : (d.state = DotState.Idle);
    });
    this._progress = 0;
  }

  addDot() {
    const pad = new DotModel();
    this._dots = [...this._dots, pad];
    pad.initialize();
  }

  initialize() {
    this._initializeDots();
    this._progress = 0;
  }

  _initializeDots() {
    const count = 3;

    for (let i = 0; i < count; i += 1) {
      this.addDot();
    }
  }
}
