import { lego } from '@armathai/lego';
import { getOpeningPads } from '../../configs/board-config';
import { idlePadCommand } from './idle-pad-command';

export function idleInitialPadsCommand() {
  const pads = getOpeningPads();

  pads.forEach(pad => {
    const { row, column } = pad;

    lego.command
      //
      .payload(row, column)
      .execute(idlePadCommand);
  });
}
