import { lego } from '@armathai/lego';
import { getOpeningPads } from '../../configs/board-config';
import { enablePadCommand } from './enable-pad-command';

export function enableInitialPadsCommand() {
  const pads = getOpeningPads();

  pads.forEach(pad => {
    const { row, column } = pad;

    lego.command
      //
      .payload(row, column)
      .execute(enablePadCommand);
  });
}
