/* eslint-disable class-methods-use-this */
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getBackgroundGridConfig } from '../../configs/grid-configs';
import { makeGradient } from '../../utils';

export class BackgroundView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    this._build();
  }

  get name() {
    return 'BackgroundView';
  }

  getGridConfig() {
    return getBackgroundGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    this._gradient && this._rebuildGradient();

    super.rebuild(config);
  }

  _rebuildGradient() {
    this._destroyGradient();
    this._buildGradient();
  }

  _build() {
    this._buildGradient();
  }

  _buildGradient() {
    this._gradient && this._gradient.destroy();
    this._gradient = makeGradient({
      width: CI_API.game.width,
      height: CI_API.game.height,
      horizontal: 0,
      vertical: 1,
      colors: [
        {
          percent: 0,
          color: '#282167'
        },
        {
          percent: 0.25,
          color: '#020339'
        },
        {
          percent: 0.75,
          color: '#020339'
        },
        {
          percent: 1,
          color: '#282167'
        }
      ]
    });

    this._gradient.inputEnabled = true;
    this._gradient.name = 'background';

    this.setChild('bg', this._gradient);
  }

  _destroyGradient() {
    this._gradient.destroy();
    this._gradient = null;
  }
}
