import { SoundPartState } from '../../../constants';
import { ObservableModel } from '../../observable-model';

export class SoundPartModel extends ObservableModel {
  constructor(name, startTime) {
    super('SoundPartModel');

    this._name = name;
    this._startTime = startTime;
    this._state = SoundPartState.Unknown;

    this.makeObservable();
  }

  get name() {
    return this._name;
  }

  get startTime() {
    return this._startTime;
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  initialize() {
    this._state = SoundPartState.Idle;
  }
}
