/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Column, Row } from '../../../configs/board-config';
import { getBallImageConfig, getHandImageConfig } from '../../../configs/image-configs';
import { BoardState, PadGap, PadHeight, PadState, PadWidth, SoundPartState } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { store } from '../../../models/store';
import { getRetryDelay, makeImage, pulse } from '../../../utils';
import { difference } from '../../../utils/array/difference';
import { Container } from '../../../utils/container';
import { PadView } from './pad-view';

export class BoardView extends Container {
  constructor() {
    super();

    this._pads = [];

    this._build();

    lego.event
      .on(ModelEvents.BoardModel.PadsUpdate, this._onPadsUpdate, this)
      .on(ModelEvents.BoardModel.StateUpdate, this._onBoardStateUpdate, this)
      .on(ModelEvents.PadModel.StateUpdate, this._onPadStateUpdate, this)
      .on(ModelEvents.SoundPartModel.StateUpdate, this._onSoundStateUpdate, this)
      .on(ModelEvents.TutorialModel.CurrentIndexUpdate, this._onTutorialIndexUpdate, this)
      .on(ModelEvents.AdModel.HintUpdate, this._onHintUpdate, this);
  }

  get name() {
    return 'BoardView';
  }

  getBounds() {
    const width = Column * PadWidth + PadGap * 2;
    const height = Row * PadHeight + PadGap * 3;

    return new Phaser.Rectangle(
      ...LP([-height / 2, -width / 2, height, width], [-width / 2, -height / 2, width, height])
    );
  }

  rebuild() {
    this._hintView && (this._hintView.angle = LP(-120, -30));
  }

  _getPadByUuid(uuid) {
    return this._pads.find(p => p.uuid === uuid);
  }

  _getPadByPosition(row, column) {
    return this._pads.find(p => p.row === row && p.column === column);
  }

  _onPadsUpdate(pads) {
    this._buildPads(pads);
  }

  _onBoardStateUpdate(state) {
    switch (state) {
      case BoardState.Playing:
        this._hintView && this._removeHintPulse();
        break;
      default:
        break;
    }
  }

  _onTutorialIndexUpdate(index) {
    switch (index) {
      case 0:
      case 2:
        this._buildBall();
        this._showBall(index);
        break;
      default:
        break;
    }
  }

  _onPadStateUpdate(state, previousState, uuid) {
    const pad = this._getPadByUuid(uuid);

    switch (state) {
      case PadState.Idle:
        pad.idle();
        break;
      case PadState.Blocked:
        pad.block();
        break;
      case PadState.Enabled:
        pad.enable();
        break;
      case PadState.Disabled:
        pad.disable();
        break;
      default:
        break;
    }
  }

  _onPadClick(uuid) {
    const { sound } = store.game.boardModel.getPadByUuid(uuid);

    lego.event.emit(ViewEvents.BoardView.PadClick, uuid, sound);
  }

  _onSoundStateUpdate(state, previousState, uuid) {
    switch (state) {
      case SoundPartState.Simulate:
        this._ballMovement();
        this._simulateClick(uuid);
        break;
      case SoundPartState.Hint:
        this._startHintMovement();
        break;
      default:
        break;
    }
  }

  _ballMovement() {
    const simulationSound = store.game.boardModel.getSimulationSoundByState(SoundPartState.Idle);

    if (simulationSound) {
      const { name, startTime } = simulationSound;
      const { uuid: padUuid } = store.game.boardModel.getPadBySound(name);
      const position = store.game.boardModel.simulationSequence.map(e => e.state).indexOf(SoundPartState.Idle);
      const previousPart = store.game.boardModel.simulationSequence[position - 1];

      const duration = startTime - previousPart.startTime;

      const pad = this._getPadByUuid(padUuid);
      const { x, y } = pad.position;

      this._moveBall(x, y, duration);
    } else {
      this._hideBall().onComplete.add(this._destroyBall, this);
    }
  }

  _simulateClick(uuid) {
    const { name } = store.game.boardModel.getSimulationSoundByUuid(uuid);
    const { uuid: padUuid } = store.game.boardModel.getPadBySound(name);

    const pad = this._getPadByUuid(padUuid);
    pad.click();
  }

  _buildBall() {
    const { name } = store.game.boardModel.simulationSequence[0];
    const { uuid } = store.game.boardModel.getPadBySound(name);
    const { x, y } = this._getPadByUuid(uuid).position;

    this._ball = makeImage(getBallImageConfig());
    this._ball.scale.set(0.8);
    this._ball.position.set(x + 35, y - 35);

    this.addChild(this._ball);
  }

  _showBall(index) {
    let delay;

    if (index === 0) {
      delay = getRetryDelay(store.ad.retries);
    } else {
      delay = 500;
    }

    this._ballShowTw = this.game.add.tween(this._ball).from({ alpha: 0 }, 300, Phaser.Easing.Linear.None, true, delay);

    return this._ballShowTw;
  }

  _hideBall() {
    this._ballHideTw = this.game.add.tween(this._ball).to({ alpha: 0 }, 300, Phaser.Easing.Linear.None, true);

    return this._ballHideTw;
  }

  _destroyBall() {
    this._ball.destroy();
    this._ball = null;
  }

  _moveBall(x, y, duration) {
    const xArr = [(x + 35) / 1, x + 35];
    const yArr = [(y - 35) / 1.5, y - 35];
    this._ballMoveTw = this.game.add
      .tween(this._ball)
      .to({ x: xArr, y: yArr }, duration, Phaser.Easing.Linear.None, true, 0);
    this._ballMoveTw.interpolation(Phaser.Math.bezierInterpolation);

    const scaleArr = [0.8, 0.6, 0.8];
    this._ballScaleTw = this.game.add
      .tween(this._ball.scale)
      .to({ x: scaleArr, y: scaleArr }, duration, Phaser.Easing.Linear.None, true, 0);
    this._ballScaleTw.interpolation(Phaser.Math.bezierInterpolation);

    this._ballMoveTw.universal = true;
    this._ballScaleTw.universal = true;
  }

  // HINT
  _onHintUpdate(hint) {
    hint && this._buildHint();
  }

  _buildHint() {
    const { name } = store.game.boardModel.playSequence[0];
    const { uuid } = store.game.boardModel.getPadBySound(name);
    const { x, y } = this._getPadByUuid(uuid).position;

    this._hintView = makeImage(getHandImageConfig());
    this._hintView.scale.set(0.9);
    this._hintView.anchor.set(0.4, 0);
    this._hintView.position.set(x, y);
    this._hintView.angle = LP(-120, -30);

    this._hintPulseTw = pulse(this._hintView, 0.1);
    this.game.add.tween(this._hintView).from({ alpha: 0 }, 500, Phaser.Easing.Linear.None, true, 500);

    this.addChild(this._hintView);
  }

  _destroyHint() {
    this._hintView.destroy();
    this._hintView = null;
  }

  _hideHint() {
    this.game.tweens.remove(this._hintPulseTw);
    this.game.tweens.remove(this._hintScaleTw);
    this.game.tweens.remove(this._hintMoveTw);

    this._hintHideTw = this.game.add.tween(this._hintView).to({ alpha: 0 }, 300, Phaser.Easing.Linear.None, true);

    return this._hintHideTw;
  }

  _removeHintPulse() {
    this._hintView.scale.set(0.9);
    this.game.tweens.remove(this._hintPulseTw);
  }

  _startHintMovement() {
    const simulationSound = store.game.boardModel.getSimulationSoundByState(SoundPartState.Idle);

    if (simulationSound) {
      const { name, startTime } = simulationSound;
      const { uuid: padUuid } = store.game.boardModel.getPadBySound(name);
      const position = store.game.boardModel.simulationSequence.map(e => e.state).indexOf(SoundPartState.Idle);
      const previousPart = store.game.boardModel.simulationSequence[position - 1];

      const duration = startTime - previousPart.startTime;

      const pad = this._getPadByUuid(padUuid);
      const { x, y } = pad.position;

      this._moveHint(x, y, duration);
    } else {
      this._hideHint().onComplete.add(this._destroyHint, this);
    }
  }

  _moveHint(x, y, duration) {
    const xArr = [x / 1, x];
    const yArr = [y / 1.5, y];
    this._hintMoveTw = this.game.add
      .tween(this._hintView)
      .to({ x: xArr, y: yArr }, duration, Phaser.Easing.Linear.None, true, 0);
    this._hintMoveTw.interpolation(Phaser.Math.bezierInterpolation);

    const scaleArr = [0.6, 0.9, 0.6];
    this._hintScaleTw = this.game.add
      .tween(this._hintView.scale)
      .to({ x: scaleArr, y: scaleArr }, duration, Phaser.Easing.Linear.None, true, 0);
    this._hintScaleTw.interpolation(Phaser.Math.bezierInterpolation);

    this._hintMoveTw.universal = true;
    this._hintScaleTw.universal = true;
  }

  _build() {
    // this._buildBounds();
  }

  _buildBounds() {
    const { x, y, width, height } = this.getBounds();
    const gr = this.game.add.graphics();
    gr.beginFill(0xff0000, 0.5);
    gr.drawRect(x, y, width, height);
    gr.endFill();
    this.addChild(gr);
  }

  _buildPads(pads) {
    const padsModels = pads.map(p => p.uuid);
    const padsView = this._pads.map(p => p.uuid);

    const newPads = difference(padsModels, padsView);

    newPads.forEach(uuid => {
      const { row, column } = store.game.boardModel.getPadByUuid(uuid);
      const pad = new PadView(uuid);

      pad.onClick.add(this._onPadClick, this);
      pad.position.set(
        column * (PadWidth + PadGap) - (PadWidth + PadGap),
        row * (PadHeight + PadGap) - (PadHeight + PadGap) * 1.5
      );

      this._pads.push(pad);
      this.addChild(pad);
    });
  }
}
