/* eslint-disable class-methods-use-this */
import { getCtaLogoLabelTextConfig } from '../../configs/text-configs';
import { makeText, searchAtlasByFrame } from '../../utils';

export class CtaLogoView extends Phaser.Sprite {
  constructor() {
    const texture = searchAtlasByFrame('ui/logo.png');
    super(CI_API.game, 0, 0, texture.key, texture.frame);
    this.name = 'LogoView';
    this.anchor.set(0.5);

    this._buildLabel();
    this._buildBounds();
  }

  getBounds() {
    return new Phaser.Rectangle(-200, -80, 400, 200);
  }

  _buildLabel() {
    const label = makeText(getCtaLogoLabelTextConfig());
    label.anchor.set(0.5);
    label.y = this.height * 0.8;
    this.addChild(label);
  }

  _buildBounds() {
    const { x, y, width, height } = this.getBounds();
    const gr = this.game.add.graphics();
    gr.beginFill(0xff0000, 0);
    gr.drawRect(x, y, width, height);
    gr.endFill();

    gr.inputEnabled = true;
    gr.name = 'logo';

    this.addChild(gr);
  }
}
