import { SoundPartState } from '../../constants';
import { store } from '../../models/store';

export function checkForHintCommand(timePast) {
  store.game.boardModel.simulationSequence.forEach(sound => {
    const { startTime } = sound;

    if (timePast > startTime && sound.state !== SoundPartState.Hint) {
      sound.state = SoundPartState.Hint;
    }
  });
}
