import { lego } from '@armathai/lego';
import { AdStatus, SoundPartState } from '../constants';
import { ModelEvents } from '../events/model-events';
import { ViewEvents } from '../events/view-events';
import { store } from '../models/store';

let sfx;

function play(id, volume = 1) {
  sfx.play(id, volume);
}

function stop(id) {
  sfx.stop(id);
}

function onLoadCompleteUpdate(complete) {
  if (!complete) {
    return;
  }

  sfx = soundLoader.addAudioSprite('audio');
}

function onAdStatusUpdate(status) {
  switch (status) {
    case AdStatus.Game:
      stop('faded_preview');
      stop('closer_preview');
      break;
    case AdStatus.PreCta:
      stop('first_sequence');
      stop('second_sequence');
      stop('drum_1');
      stop('drum_2');
      stop('drum_3');
      stop('missing_part_1');
      stop('missing_part_2');
      break;
    default:
      break;
  }
}

function onPadClick(uuid, sound) {
  play(sound);
}

function onSoundPartStateUpdate(state, previousState, uuid) {
  switch (state) {
    case SoundPartState.Autoplay:
      play(store.game.boardModel.getAutoPlaySoundByUuid(uuid).name);
      break;
    case SoundPartState.Simulate:
      play(store.game.boardModel.getSimulationSoundByUuid(uuid).name);
      break;
    default:
      break;
  }
}

function onOptionPreview(option) {
  switch (option) {
    case 0:
      play('faded_preview');
      break;
    case 1:
      play('closer_preview');
      break;
    default:
      break;
  }
}

export function SoundObservant() {
  lego.event
    .on(ModelEvents.LoadModel.CompleteUpdate, onLoadCompleteUpdate, this)
    .on(ModelEvents.AdModel.StatusUpdate, onAdStatusUpdate, this)
    .on(ViewEvents.BoardView.PadClick, onPadClick, this)
    .on(ModelEvents.SoundPartModel.StateUpdate, onSoundPartStateUpdate, this)
    .on(ViewEvents.CtaView.OptionPreview, onOptionPreview, this);
}
