import { lego } from '@armathai/lego';
import { getGameTime, getSequenceStartEndTime } from '../../configs/board-config';
import { BoardState } from '../../constants';
import { tutorialModelGuard } from '../../guards/ad/tutorial-model-guard';
import { tutorialSequenceGuard } from '../../guards/ad/tutorial-sequence-guard';
import { boardStateIdleGuard } from '../../guards/board/board-state-idle-guard';
import { store } from '../../models/store';
import { completeTutorialSequenceCommand } from '../ad/tutorial/complete-tutorial-sequence-command';
import { startTimerFromCommand } from '../game/start-timer-from-command';
import { checkForRightPadClickedCommand } from './check-for-right-pad-clicked-command';
import { setBoardStateCommand } from './set-board-state-command';

export function onPadClickCommand(uuid) {
  const pad = store.game.boardModel.getPadByUuid(uuid);
  const { startTime } = getSequenceStartEndTime(store.game.boardModel.sequence);

  lego.command
    //
    .payload(1)
    .guard(tutorialModelGuard, tutorialSequenceGuard, boardStateIdleGuard)
    .execute(completeTutorialSequenceCommand)

    .payload(3)
    .guard(tutorialModelGuard, tutorialSequenceGuard, boardStateIdleGuard)
    .execute(completeTutorialSequenceCommand)

    .payload(startTime, false)
    .guard(boardStateIdleGuard)
    .execute(startTimerFromCommand)

    .payload(BoardState.Playing)
    .execute(setBoardStateCommand)

    .payload(pad.sound, getGameTime() - store.game.timerModel.remaining)
    .execute(checkForRightPadClickedCommand);
}
