/* eslint-disable no-nested-ternary */
/* eslint-disable class-methods-use-this */
import { InputPriority } from '../../constants';
import LU from '../../display/layout-utils';
import { getViewByProperty, isSquareLikeScreen, makeImage, postRunnable } from '../../utils/index';

export class SoundIconBuilder {
  constructor() {
    this._onBtn = null;

    this._build();
  }

  onResize() {
    this._setPosition();
  }

  _build() {
    this._onBtn = makeImage({ frame: 'sound_on.png' });
    this._onBtn.scale.set(0.8);
    soundLoader.registerMuteButton(this._onBtn);

    this._onBtn.name = 'mute_button';
    this._onBtn.input.priorityID = InputPriority.Foreground;
    CI_API.game.world.add(this._onBtn);

    postRunnable(() => {
      LU.resizeCall(CI_API.game.world);
    });
  }

  _setPosition() {
    postRunnable(() => {
      let x;
      const { logoPosition } = getViewByProperty('name', 'ForegroundView');

      if (logoPosition) {
        x = LP(logoPosition.x, CI_API.game.width - this._onBtn.width / 2 - 30);
      } else {
        const { logoPosition: ctaLogoPos } = getViewByProperty('name', 'CTAView');
        x = LP(ctaLogoPos.x, CI_API.game.width - this._onBtn.width / 2 - 30);
      }

      const y = LP(CI_API.game.height / 2 + 15, this._onBtn.height / 2 + (isSquareLikeScreen() ? 0 : 15));

      this._onBtn.x = x;
      this._onBtn.y = y;
    });
  }
}
