import { SoundPartState } from '../../constants';
import { store } from '../../models/store';

export function checkForAutoPlaySoundsCommand(timePast) {
  store.game.boardModel.autoPlaySounds.forEach(sound => {
    const { startTime } = sound;

    if (timePast > startTime && sound.state !== SoundPartState.Autoplay) {
      sound.state = SoundPartState.Autoplay;
    }
  });
}
