import { getGameTime } from '../../configs/board-config';
import { delayRunnable, loopRunnable, removeRunnable } from '../../utils';
import { ObservableModel } from '../observable-model';

export class TimerModel extends ObservableModel {
  constructor(delta = CI_API.game.time.physicsElapsedMS) {
    super('TimerModel');

    this._delta = delta;
    this._timer = null;
    this._duration = null;
    this._remaining = null;
    this._complete = null;
    this._stopped = false;

    this.makeObservable();
  }

  get timer() {
    return this._timer;
  }

  get remaining() {
    return this._remaining;
  }

  get duration() {
    return this._duration;
  }

  get delta() {
    return this._delta;
  }

  get complete() {
    return this._complete;
  }

  set complete(value) {
    this._complete = value;
  }

  get stopped() {
    return this._stopped;
  }

  set stopped(value) {
    this._stopped = value;
  }

  destroy() {
    this.stopTimer();
  }

  pause() {
    this.stopTimer();
  }

  resume() {
    this._startTimer();
  }

  startTimer() {
    this._stopped = false;
    this._duration = getGameTime();
    this._remaining = this._duration;
    this._startTimer();
  }

  startTimerFrom(time, delay) {
    this._stopped = false;
    this._duration = getGameTime();
    this._remaining = getGameTime() - time;

    if (delay) {
      this._startRunnable = delayRunnable(500, () => {
        this._startTimer();
      });
    } else {
      this._startTimer();
    }
  }

  stopTimer() {
    this._stopped = true;
    this._complete = false;

    if (this._timer) {
      removeRunnable(this._timer);
    }
  }

  _startTimer() {
    this._timer = loopRunnable(this._delta, () => {
      this._remaining = Math.max(this._remaining - this._delta, 0);
      if (this._remaining <= 0) {
        this._remaining = 0;
        this._complete = true;
        this.stopTimer();
      }
    });
  }
}
