/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getUIGridConfig } from '../../configs/grid-configs';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { getRetryDelay, hideToBottom, showFromBottom } from '../../utils';
import { PersistentCTAView } from './persistent-cta-view';

export class UIView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    lego.event.on(ModelEvents.AdModel.PersistentCtaUpdate, this._onPersistentCtaUpdate, this);
  }

  get name() {
    return 'UIView';
  }

  getGridConfig() {
    return getUIGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    this._persistentCtaView && this._persistentCtaView.rebuild();

    super.rebuild(config);
  }

  // PERSISTENT
  _onPersistentCtaUpdate(persistentCta) {
    if (persistentCta) {
      this.game.tweens.remove(this._hideTw);
      this._hideTw = null;
      this._persistentCtaView && this._destroyPersistentCta();
      this._buildPersistentCta();
      this._showPersistentCta();
    } else {
      this.game.tweens.remove(this._showTw);
      this._showTw = null;
      this._hidePersistentCta().onComplete.add(this._destroyPersistentCta, this);
    }
  }

  _buildPersistentCta() {
    this.setChild('p_cta', (this._persistentCtaView = new PersistentCTAView()));
  }

  _destroyPersistentCta() {
    this._persistentCtaView.destroy();
    this._persistentCtaView = null;
  }

  _showPersistentCta() {
    const twArg = [this._persistentCtaView, getRetryDelay(store.ad.retries), 500, false, Phaser.Easing.Cubic.Out];
    this._showTw = LP(showFromBottom, showFromBottom).call(null, ...twArg);
    return this._showTw;
  }

  _hidePersistentCta() {
    const twArg = [this._persistentCtaView, 0, 500, false, Phaser.Easing.Cubic.In];
    this._hideTw = LP(hideToBottom, hideToBottom).call(null, ...twArg);
    return this._hideTw;
  }
}
