import { lego, not } from '@armathai/lego';
import { getSequenceStartEndTime } from '../../configs/board-config';
import { BoardState } from '../../constants';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { hintParamGuard } from '../../guards/ad/hint-param-guard';
import { lastTutorialSequenceGuard } from '../../guards/ad/tutorial-last-sequence-guard';
import { store } from '../../models/store';
import { initializeHintModelCommand } from '../ad/hint/initialize-hint-model-command';
import { nextTutorialSequenceCommand } from '../ad/tutorial/next-tutorial-sequence-command';
import { showTutorialSequenceCommand } from '../ad/tutorial/show-tutorial-sequence-command';
import { stopTimerCommand } from '../game/stop-timer-command';
import { enableInitialPadsCommand } from './enable-initial-pads-command';
import { resetAutoPlaySoundsCommand } from './reset-auto-play-sounds-command';
import { resetSimulationSequenceCommand } from './reset-simulation-sequence-command';
import { setBoardStateCommand } from './set-board-state-command';

export function checkForSimulationSequenceOverCommand(timePast) {
  const { endTime } = getSequenceStartEndTime(store.game.boardModel.sequence);

  if (timePast >= endTime) {
    lego.command
      //
      .payload(BoardState.Idle)
      .execute(setBoardStateCommand)

      .execute(stopTimerCommand)

      .guard(not(lastTutorialSequenceGuard))
      .execute(nextTutorialSequenceCommand, showTutorialSequenceCommand)

      .guard(hintParamGuard, not(hintModelGuard))
      .execute(initializeHintModelCommand)

      .execute(resetAutoPlaySoundsCommand, resetSimulationSequenceCommand)

      .execute(enableInitialPadsCommand);
  }
}
