import { lego, not } from '@armathai/lego';
import { getGameTime } from '../../configs/board-config';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { tutorialModelGuard } from '../../guards/ad/tutorial-model-guard';
import { boardStateIdleGuard } from '../../guards/board/board-state-idle-guard';
import { boardStatePlayingGuard } from '../../guards/board/board-state-playing-guard';
import { boardStateSimulationGuard } from '../../guards/board/board-state-simulation-guard';
import { checkForAutoPlaySoundsCommand } from '../board/check-for-auto-play-sound-parts-command';
import { checkForSequencePlayedCommand } from '../board/check-for-sequence-played-command';
import { checkForSimulationCommand } from '../board/check-for-simulation-command';
import { checkForSimulationSequenceOverCommand } from '../board/check-for-simulation-sequence-over-command';
import { checkForHintCommand } from '../board/check-hint-simulation-command';
import { updateProgressCommand } from './update-progress-command';

export function onTimerRemainingUpdateCommand(remaining) {
  lego.command
    //
    .payload(getGameTime() - remaining)
    .guard(boardStateSimulationGuard)
    .execute(checkForSimulationSequenceOverCommand)

    .payload(getGameTime() - remaining)
    .guard(boardStateSimulationGuard)
    .execute(checkForSimulationCommand)

    .payload(getGameTime() - remaining)
    .guard(not(boardStateIdleGuard))
    .execute(checkForAutoPlaySoundsCommand)

    .payload(getGameTime() - remaining)
    .guard(hintModelGuard, boardStatePlayingGuard)
    .execute(checkForHintCommand)

    .payload(getGameTime() - remaining)
    .guard(tutorialModelGuard, boardStatePlayingGuard)
    .execute(checkForSequencePlayedCommand)

    .payload(remaining)
    .guard(boardStatePlayingGuard)
    .execute(updateProgressCommand);
}
