/* eslint-disable class-methods-use-this */
import { getCtaPlayButtonConfig } from '../../configs/button-configs';
import { getCTAEmbeddedGridConfig } from '../../configs/grid-configs';
import { store } from '../../models/store';
import { delayRunnable, hideToBottom, hideToUp, removeRunnable, showFromBottom, showFromUp } from '../../utils';
import { CTAEmbeddedContent } from './cta-embedded-contnent';
import { CTAView } from './cta-view';
import { NextButton } from './next-button';

export class CTAEmbeddedView extends CTAView {
  constructor() {
    super();

    this._build();
    this.show();
  }

  getGridConfig() {
    return getCTAEmbeddedGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  destroy() {
    removeRunnable(this._showRunnable);

    super.destroy();
  }

  buildPlayButton(cellName = 'play_button') {
    this._playBtn = new NextButton(getCtaPlayButtonConfig('NEXT SONG'));
    this._playBtn.onClick.add(this._onPlayClick, this);
    this._playBtn.alpha = 0;

    this.setChild(cellName, this._playBtn);
  }

  show() {
    this._showRunnable = delayRunnable(500, () => {
      this._content.show();
      this._logo.alpha = 1;
      this._playBtn.alpha = 1;
      this.showBlocker().onComplete.add(() => {
        if (CI_API.Globals.PARAMS.cta_scrn_clickable) {
          this._blocker.events.onInputDown.add(this._onScreenClick, this);
        }
      });
      showFromBottom(this._playBtn, 200, 500, false, Phaser.Easing.Cubic.Out).onComplete.add(() => {
        this._playBtn.onClick.add(this._onPlayClick, this);
      });
      showFromUp(this._logo, 0, 500, false, Phaser.Easing.Sinusoidal.Out);
      if (this._retryBtn) {
        this._retryBtn.alpha = 1;
        showFromBottom(this._retryBtn, 0, 500, false, Phaser.Easing.Cubic.Out).onComplete.add(() => {
          this._retryBtn.onClick.add(this._onRetryClick, this);
        });
      }
    });
  }

  hide() {
    this.hideBlocker();
    hideToBottom(this._playBtn, 0, 500, false, Phaser.Easing.Cubic.In).onComplete.add(() => {
      this._playBtn.visible = false;
    });
    hideToUp(this._logo, 0, 500, false, Phaser.Easing.Cubic.In).onComplete.add(() => {
      this._logo.visible = false;
    });

    const longestTween = this._content.hide();
    if (this._retryBtn) {
      hideToBottom(this._retryBtn, 200, 500, false, Phaser.Easing.Cubic.In).onComplete.add(() => {
        this._retryBtn.visible = false;
      });
    }

    return longestTween;
  }

  _build() {
    this.build(this.getGridConfig());
    this.buildLogo();
    this._buildContent();
    if (store.ad.retries) {
      this.buildPlayButton();
      this.buildRetryButton();
    } else {
      this.buildPlayButton('play_button_no_retry');
    }
  }

  _buildContent() {
    this.setChild('content', (this._content = new CTAEmbeddedContent()));
  }
}
