import { DotState } from '../../constants';
import { store } from '../../models/store';

export function updateProgressBarDots(progress) {
  if (progress === 0) {
    const firstDot = store.game.progressBarModel.getDotAtPosition(0);
    firstDot.state = DotState.Filled;

    const secondDot = store.game.progressBarModel.getDotAtPosition(1);
    secondDot.state = DotState.Glowing;
  } else if (progress >= 0.5 && progress < 1) {
    const firstDot = store.game.progressBarModel.getDotAtPosition(1);

    if (firstDot.state !== DotState.Filled) {
      firstDot.state = DotState.Idle;
      firstDot.state = DotState.Filled;
    }

    const secondDot = store.game.progressBarModel.getDotAtPosition(2);
    secondDot.state = DotState.Glowing;
  } else if (progress >= 1) {
    const firstDot = store.game.progressBarModel.getDotAtPosition(2);
    firstDot.state = DotState.Idle;
    firstDot.state = DotState.Filled;
  }
}
