import { lego, not } from '@armathai/lego';
import { tutorialModelGuard } from '../../guards/ad/tutorial-model-guard';
import { boardStatePlayingGuard } from '../../guards/board/board-state-playing-guard';
import { timerCompleteGuard } from '../../guards/game/timer-complete-guard';
import { timerToCtaCommand } from './timer-to-cta-command';

export function onTimerCompleteUpdateCommand(complete) {
  lego.command
    //
    .payload(complete)
    .guard(not(tutorialModelGuard), timerCompleteGuard, boardStatePlayingGuard)
    .execute(timerToCtaCommand);
}
