import { lego } from '@armathai/lego';
import { getSequenceStartEndTime } from '../../configs/board-config';
import { store } from '../../models/store';
import { startTimerFromCommand } from '../game/start-timer-from-command';
import { boardSimulateCommand } from './board-simulate-command';

export function startSimulationCommand() {
  const { startTime } = getSequenceStartEndTime(store.game.boardModel.sequence);

  lego.command
    //
    .execute(boardSimulateCommand)

    .payload(startTime, true)
    .execute(startTimerFromCommand);
}
