import { getPlaySequence } from '../../configs/board-config';
import { getEmbeddedCTAEmitterConfig } from '../../configs/particles-configs';
import {
  getCTAEmbeddedProgressTextConfig,
  getCTAStandardLabelTextConfig as getCTAEmbeddedLabelTextConfig
} from '../../configs/text-configs';
import { store } from '../../models/store';
import { makeEmitter, makeText } from '../../utils';
import { Container } from '../../utils/container';
import { CTAProgressComponent } from './cta-progress-component';

/* eslint-disable class-methods-use-this */
export class CTAEmbeddedContent extends Container {
  constructor() {
    super();
    this._buildLabel();
    this._buildProgress();
    this._buildProgressLabel();
    this._buildParticles();
    this.alpha = 0;

    // const gr = this.game.add.graphics();
    // gr.beginFill(0xff0000, 0.5);
    // gr.drawRect(-270, -300, 540, 600);
    // this.add(gr);
  }

  getBounds() {
    return new Phaser.Rectangle(-270, -300, 540, 600);
  }

  destroy() {
    this.game.tweens.remove(this._hideTw);
    if (this._particleEmitter) {
      this._particleEmitter.paused = true;
      this._particleEmitter.destroy();
    }

    super.destroy();
  }

  show() {
    this.game.add.tween(this).to({ alpha: 1 }, 500, Phaser.Easing.Sinusoidal.InOut, true);
  }

  hide() {
    this._hideTw = this.game.add.tween(this).to({ alpha: 0 }, 800, Phaser.Easing.Sinusoidal.InOut, true);

    return this._hideTw;
  }

  _buildProgress() {
    let length = 0;

    const sequence = getPlaySequence();

    if (CI_API.Globals.PARAMS.short_version) {
      length = sequence[0].length;
    } else {
      sequence.forEach(s => {
        length += s.length;
      });
    }

    const percent = Math.max(0.5, store.player.score / length);

    const progress = new CTAProgressComponent(percent);
    this.addChild(progress);
  }

  _buildProgressLabel() {
    let length = 0;

    const sequence = getPlaySequence();

    if (CI_API.Globals.PARAMS.short_version) {
      length = sequence[0].length;
    } else {
      sequence.forEach(s => {
        length += s.length;
      });
    }

    const percent = Math.max(0.5, store.player.score / length);

    const label = makeText(getCTAEmbeddedProgressTextConfig(percent));
    label.anchor.set(0.5);
    label.y = 100;
    this.addChild(label);
  }

  _buildParticles() {
    this._particlesContainer = new Container();
    this.addChildAt(this._particlesContainer, 0);

    const gr = this.game.add.graphics();
    gr.beginFill(0x00ff00, 0);
    gr.drawRect(-300, -350, 600, 700);
    this.add(gr);
    this._particleEmitter = makeEmitter(getEmbeddedCTAEmitterConfig(this._particlesContainer, gr));
  }

  _buildLabel() {
    const label = makeText(getCTAEmbeddedLabelTextConfig());
    label.anchor.set(0.5);
    label.y = -80;
    this.addChild(label);
  }
}
