import { DotState } from '../../constants';
import { ObservableModel } from '../observable-model';

export class DotModel extends ObservableModel {
  constructor() {
    super('DotModel');

    this._state = DotState.Unknown;

    this.makeObservable();
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  initialize() {
    this._state = DotState.Idle;
  }
}
