/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getForegroundGridConfig } from '../../configs/grid-configs';
import { AdStatus } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { getRetryDelay, hideToUp, showFromUp } from '../../utils';
import { CtaLogoView } from '../cta/cta-logo-view';
import { LogoView } from './logo-view';
import { TutorialView } from './tutorial-view';

function getLogo() {
  if (LP(true, false)) {
    return new CtaLogoView();
  }
  return new LogoView();
}

export class ForegroundView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    lego.event
      .on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this)
      .on(ModelEvents.AdModel.TutorialUpdate, this._onTutorialUpdate, this);
  }

  get name() {
    return 'ForegroundView';
  }

  get logoPosition() {
    return this._logoView ? this.toGlobal(this._logoView.position) : null;
  }

  getGridConfig() {
    return getForegroundGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    if (this._logoView) {
      this._destroyLogo();
      this.setChild('logo', (this._logoView = getLogo()));
    }

    super.rebuild(config);
  }

  _removeTweens() {
    this.game.tweens.remove(this._tutorialHideTw);
    this.game.tweens.remove(this._logoShowTw);
    this.game.tweens.remove(this._logoHideTw);
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.PreCta:
        this._removeTweens();
        this._tutorialView && this._destroyTutorial();

        this._hideLogo().onComplete.add(this._destroyLogo, this);
        break;
      case AdStatus.Game:
        this._build();
        this._removeTweens();

        this._logoView && this._destroyLogo();
        this._buildLogo();
        this._showLogo();
        break;
      default:
    }
  }

  _build() {
    //
  }

  // LOGO
  _buildLogo() {
    this._logoView = getLogo();
    this.setChild('logo', this._logoView);
  }

  _destroyLogo() {
    this._logoView.destroy();
    this._logoView = null;
  }

  _showLogo() {
    const twArg = [this._logoView, getRetryDelay(store.ad.retries), 500, false, Phaser.Easing.Cubic.Out];
    this._logoShowTw = LP(showFromUp, showFromUp).call(null, ...twArg);
    return this._logoShowTw;
  }

  _hideLogo() {
    const twArg = [this._logoView, 0, 500, false, Phaser.Easing.Cubic.In];
    this._logoHideTw = LP(hideToUp, hideToUp).call(null, ...twArg);
    return this._logoHideTw;
  }

  // TUTORIAL
  _onTutorialUpdate(tutorial) {
    if (tutorial) {
      this._buildTutorial();
    } else if (this._tutorialView) {
      this._tutorialHideTw = this._hideTutorial();
      this._tutorialHideTw.onComplete.add(() => this._destroyTutorial());
    }
  }

  _onTutorialSequenceReady() {
    this.setChild('tutorial', this._tutorialView);
  }

  _buildTutorial() {
    this._tutorialView = new TutorialView();
    this._tutorialView.onSequenceReady.add(this._onTutorialSequenceReady, this);
  }

  _destroyTutorial() {
    this._tutorialView.destroy();
    this._tutorialView = null;
  }

  _hideTutorial() {
    const tw = this.game.add.tween(this._tutorialView).to({ alpha: 0 }, 500, Phaser.Easing.Sinusoidal.InOut, true);
    tw.universal = true;

    return tw;
  }
}
