import { SoundPartState } from '../../constants';
import { store } from '../../models/store';

export function checkForSimulationCommand(timePast) {
  store.game.boardModel.simulationSequence.forEach(sound => {
    const { startTime } = sound;

    if (timePast > startTime && sound.state !== SoundPartState.Simulate) {
      sound.state = SoundPartState.Simulate;
    }
  });
}
