import { lego } from '@armathai/lego';
import { getOpeningPads } from '../../configs/board-config';
import { disablePadCommand } from './disable-pad-command';

export function disableInitialPadsCommand() {
  const pads = getOpeningPads();

  pads.forEach(pad => {
    const { row, column } = pad;

    lego.command
      //
      .payload(row, column)
      .execute(disablePadCommand);
  });
}
