import { lego, not } from '@armathai/lego';
import { getSequenceStartEndTime } from '../../configs/board-config';
import { BoardState, GameOverReasons } from '../../constants';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { lastTutorialSequenceGuard } from '../../guards/ad/tutorial-last-sequence-guard';
import { tutorialModelGuard } from '../../guards/ad/tutorial-model-guard';
import { lastPlaySequenceGuard } from '../../guards/board/last-play-sequence-guard';
import { store } from '../../models/store';
import { showCtaCommand } from '../ad/cta/show-cta-command';
import { destroyHintModelCommand } from '../ad/hint/destroy-hint-model-command';
import { nextTutorialSequenceCommand } from '../ad/tutorial/next-tutorial-sequence-command';
import { showTutorialSequenceCommand } from '../ad/tutorial/show-tutorial-sequence-command';
import { stopTimerCommand } from '../game/stop-timer-command';
import { disableInitialPadsCommand } from './disable-initial-pads-command';
import { nextPlaySequenceCommand } from './next-play-sequence-command';
import { resetAutoPlaySoundsCommand } from './reset-auto-play-sounds-command';
import { resetPlaySequenceCommand } from './reset-play-sequence-command';
import { resetSimulationSequenceCommand } from './reset-simulation-sequence-command';
import { setBoardStateCommand } from './set-board-state-command';

export function checkForSequencePlayedCommand(timePast) {
  const { endTime } = getSequenceStartEndTime(store.game.boardModel.sequence);

  if (timePast >= endTime) {
    lego.command
      //
      .execute(stopTimerCommand)

      .payload(BoardState.Idle)
      .execute(setBoardStateCommand)

      .payload(GameOverReasons.ItemsToCtaReached, 500)
      .guard(lastPlaySequenceGuard)
      .execute(showCtaCommand)

      .guard(not(lastPlaySequenceGuard))
      .execute(
        nextPlaySequenceCommand,
        resetAutoPlaySoundsCommand,
        resetSimulationSequenceCommand,
        resetPlaySequenceCommand
      )

      .guard(tutorialModelGuard, not(lastTutorialSequenceGuard))
      .execute(nextTutorialSequenceCommand, showTutorialSequenceCommand)

      .guard(hintModelGuard)
      .execute(destroyHintModelCommand)

      .execute(disableInitialPadsCommand);
  }
}
