import { getStandardCtaTitleTextConfig } from '../../configs/text-configs';
import { makeText } from '../../utils';
import { Container } from '../../utils/container';

/* eslint-disable class-methods-use-this */
export class CTAStandardTitleComponent extends Container {
  constructor() {
    super();
    this._build();
  }

  _build() {
    const label1 = makeText(getStandardCtaTitleTextConfig('Choose', '#f9db0b'));
    const label2 = makeText(getStandardCtaTitleTextConfig('your next song!'));
    label2.position.set(label1.right, 0);

    this.addChild(label1);
    this.addChild(label2);
  }
}
