/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getPauseMusicImageConfig, getPlayMusicImageConfig, getSingerImageConfig } from '../../configs/image-configs';
import { getMusicBarGlowPatchConfig, getMusicBarPatchConfig } from '../../configs/ninepatch-configs';
import {
  getCtaOptionOriginalTextConfig,
  getCtaOptionSingerTextConfig,
  getCtaOptionTitleTextConfig
} from '../../configs/text-configs';
import { InputPriority } from '../../constants';
import { ViewEvents } from '../../events/view-events';
import { makeImage, makeNinePatch, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class OptionComponent extends Container {
  constructor(config) {
    super();

    this._config = config;

    this._build();
  }

  get playButton() {
    return this._playButton;
  }

  getBounds() {
    return new Phaser.Rectangle(-265, -90, 530, 180);
  }

  highlightGlow() {
    this._glow.visible = true;
    const tw = this.game.add.tween(this._glow).to({ alpha: 1 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0, 0, true);
    tw.universal = true;
    tw.onComplete.add(() => {
      this._glow.visible = false;
    });
  }

  play() {
    this._playButton.alpha = 0;
    this._pauseButton.alpha = 1;
  }

  pause() {
    this._playButton.alpha = 1;
    this._pauseButton.alpha = 0;
  }

  enable() {
    this._bg.input.priorityID = InputPriority.Cta + 1;
    this._bg.events.onInputDown.add(() => {
      lego.event.emit(ViewEvents.CtaView.OptionClick, this._bg.name);
    });
  }

  _build() {
    const { icon, title, singer } = this._config;

    this._buildBg();
    this._buildGlow();
    this._buildIcon(icon);
    this._buildTitleLabel(title);
    this._buildOriginalLabel();
    this._buildPlayButton();
    this._buildPauseButton();
    this._buildSingerLabel(singer);
  }

  _buildBg() {
    const bg = makeNinePatch(getMusicBarPatchConfig());
    const { title, singer } = this._config;
    bg.name = `${singer}: ${title}`;
    bg.inputEnabled = true;
    this.addChild((this._bg = bg));
  }

  _buildGlow() {
    const glow = makeNinePatch(getMusicBarGlowPatchConfig());
    this.addChild((this._glow = glow));
    this._glow.alpha = 0;
    this._glow.visible = false;
  }

  _buildIcon(icon) {
    const img = makeImage(getSingerImageConfig(icon));
    this.addChild((this._icon = img));
    img.left = this._bg.left + 33;
    img.y = -1;
  }

  _buildTitleLabel(text) {
    const label = makeText(getCtaOptionTitleTextConfig(text));
    label.left = this._icon.right + 30;
    label.top = this._icon.top + 30;
    this.addChild(label);
  }

  _buildOriginalLabel() {
    const label = makeText(getCtaOptionOriginalTextConfig());
    label.left = this._icon.right + 30;
    label.top = this._icon.top + 75;
    this.addChild(label);
  }

  _buildSingerLabel(text) {
    const label = makeText(getCtaOptionSingerTextConfig(text));
    label.left = this._icon.right + 30;
    label.top = this._icon.top + 105;
    this.addChild(label);
  }

  _buildPlayButton() {
    const img = makeImage(getPlayMusicImageConfig());
    this.addChild((this._playButton = img));
    img.right = this._bg.right - 70;
    img.y = -1;
  }

  _buildPauseButton() {
    const img = makeImage(getPauseMusicImageConfig());
    this.addChild((this._pauseButton = img));
    img.right = this._bg.right - 70;
    img.alpha = 0;
    img.y = -1;
  }
}
