import { CellAlign, CellScale } from '@armathai/phaser2-grid';
import { isNarrowScreen, isSquareLikeScreen } from '../../utils';

export function getCTAStandardGridLandscapeConfig() {
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        name: 'logo',
        bounds: { x: 0, y: 0.05, width: 0.3, height: 0.3 },
        padding: { x: 0.1, width: 0.8 }
      },
      {
        name: 'title',
        // align: CellAlign.CenterBottom,
        bounds: { x: 0, y: isNarrowScreen() ? 0.35 : 0.3, width: 0.3, height: 0.1 },
        padding: { x: 0.1, width: 0.8 }
      },
      {
        name: 'retry_button',
        align: CellAlign.CenterBottom,
        bounds: { x: 0, y: 0.6 - (isSquareLikeScreen() ? 0.025 : 0), width: 0.3, height: 0.15 },
        padding: { x: 0.1, width: 0.8 }
      },
      {
        name: 'play_button',
        align: CellAlign.CenterTop,
        bounds: { x: 0, y: 0.77 - (isSquareLikeScreen() ? 0.025 : 0), width: 0.3, height: 0.15 },
        padding: { x: 0.1, width: 0.8 }
      },
      {
        name: 'play_button_no_retry',
        bounds: { x: 0, y: 0.68 - (isSquareLikeScreen() ? 0.025 : 0), width: 0.3, height: 0.15 },
        padding: { x: 0.1, width: 0.8 }
      },
      {
        name: 'content',
        bounds: { x: 0.3, y: 0, width: 0.7, height: 1 },
        padding: { x: 0.1, y: 0.15, height: 0.7, width: 0.7 }
      },
      {
        name: 'blocker',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      }
    ]
  };
}

export function getCTAStandardGridPortraitConfig() {
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        name: 'logo',
        bounds: { x: 0, y: 0.05, width: 1, height: 0.18 }
      },
      {
        name: 'title',
        align: CellAlign.CenterBottom,
        bounds: { x: 0.1, y: 0.25, width: 0.8, height: 0.08 }
      },
      {
        name: 'content',
        align: CellAlign.Center,
        bounds: { x: 0.1, y: 0.35, width: 0.8, height: 0.4 - (isSquareLikeScreen() ? 0.025 : 0.05) }
      },
      {
        name: 'retry_button',
        align: CellAlign.CenterBottom,
        bounds: { x: 0.1, y: 0.77 - (isSquareLikeScreen() ? 0.025 : 0.05), width: 0.8, height: 0.1 },
        padding: { height: 0.9 }
      },
      {
        name: 'play_button',
        align: CellAlign.CenterTop,
        bounds: { x: 0.1, y: 0.77 + 0.1 - (isSquareLikeScreen() ? 0.025 : 0.05), width: 0.8, height: 0.1 },
        padding: { y: 0.1 }
      },
      {
        name: 'play_button_no_retry',
        bounds: { x: 0.1, y: 0.81 - (isSquareLikeScreen() ? 0.025 : 0.05), width: 0.8, height: 0.1 }
      },
      {
        name: 'blocker',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      }
    ]
  };
}
