/* eslint-disable class-methods-use-this */
import { getGameLogoLabelTextConfig } from '../../configs/text-configs';
import { makeText, searchAtlasByFrame } from '../../utils';

export class LogoView extends Phaser.Sprite {
  constructor() {
    const texture = searchAtlasByFrame('ui/logo.png');
    super(CI_API.game, 0, 0, texture.key, texture.frame);
    this.name = 'LogoView';
    this.anchor.set(0.5);

    this._buildLabel();
    this._buildBounds();
  }

  getBounds() {
    return new Phaser.Rectangle(-70, -80, 835, 150);
  }

  _buildLabel() {
    const label = makeText(getGameLogoLabelTextConfig());
    label.anchor.set(0, 0.5);
    label.x = this.width / 2 + 20;
    label.y = 5;
    this.addChild((this._label = label));
  }

  _buildBounds() {
    const { x, y, width, height } = this.getBounds();
    const gr = this.game.add.graphics();
    gr.beginFill(0xff0000, 0);
    gr.drawRect(x, y, width, height);
    gr.endFill();

    gr.inputEnabled = true;
    gr.name = 'logo';

    this.addChild(gr);
  }
}
