import { getProgressBarTriangleImageConfig } from '../../configs/image-configs';
import { getPointerTextConfig } from '../../configs/text-configs';
import { fitText, makeImage, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class PointerComponent extends Container {
  constructor() {
    super();

    this._build();

    this.updateText(0);
  }

  updateText(percent) {
    this._label.setText({
      text: `${parseInt(percent, 10)} %`,
      ignoreLocalization: true,
      toString: function toString() {
        return this;
      }
    });
  }

  rebuild() {
    this._repositionText();
  }

  _repositionText() {
    this._label.angle = LP(90, 0);
    this._label.anchor.set(LP(1, 0.5), LP(0.5, 1));
    this._label.position.set(LP(-2, 0), LP(this._pointer.top - 5, this._pointer.top));
  }

  _build() {
    this._buildPointer();
    this._buildText();
  }

  _buildPointer() {
    this._pointer = makeImage(getProgressBarTriangleImageConfig());
    this._pointer.anchor.set(0.5, 1);

    this.addChild(this._pointer);
  }

  _buildText() {
    this._label = makeText(getPointerTextConfig());
    this._repositionText();

    fitText(this._label, this._pointer.width * 0.95, this._pointer.height);

    this.addChild(this._label);
  }
}
