export function getPCTASparkleEmitterConfig(parent, view) {
  const { x, y, width, height } = view;
  return {
    key: sparkle,
    parent,
    x: x - width / 2,
    y: y - height / 2,
    force: { x: 0, y: 0 },
    config: {
      zone: CI_API.game.particleStorm.createRectangleZone(width, height),
      total: 10,
      repeat: -1
    }
  };
}

export function getEmbeddedCTAEmitterConfig(parent, view) {
  const { x, y, width, height } = view;
  return {
    key: ctaSparkle,
    parent,
    x: x - width / 2,
    y: y - height / 2,
    force: { x: 0, y: 0 },
    config: {
      zone: CI_API.game.particleStorm.createRectangleZone(width, height),
      total: 6,
      repeat: -1
    }
  };
}
