/* eslint-disable class-methods-use-this */
import { getPadImageConfig, getPadInnerGlowImageConfig } from '../../../configs/image-configs';
import { store } from '../../../models/store';
import { makeImage } from '../../../utils';
import { Container } from '../../../utils/container';

export class PadView extends Container {
  constructor(uuid) {
    super();

    this._uuid = uuid;
    this.onClick = new Phaser.Signal();

    this._build();
  }

  get name() {
    return 'PadView';
  }

  get uuid() {
    return this._uuid;
  }

  idle() {
    this._blocker.alpha = 0;
  }

  block() {
    this._blocker.alpha = 0.8;
  }

  enable() {
    this._bg.inputEnabled = true;
    this._bg.events.onInputDown.add(this._onClick, this);
  }

  disable() {
    this._bg.inputEnabled = false;
    this._bg.events.onInputDown.remove(this._onClick, this);
  }

  click() {
    this.scale.set(1);
    this._outline.alpha = 1;
    this._innerGlow.alpha = 1;
    this._outline.scale.set(1, 1);

    this.game.tweens.remove(this._padScaleTw);
    this.game.tweens.remove(this._outlineScaleTw);
    this.game.tweens.remove(this._outlineAlphaTw);

    this._padScaleTw = this.game.add
      .tween(this.scale)
      .to({ x: 0.8, y: 0.8 }, 100, Phaser.Easing.Cubic.InOut, true, 0, 0, true);

    this._outlineScaleTw = this.game.add
      .tween(this._outline.scale)
      .to({ x: 1.5, y: 1.5 }, 500, Phaser.Easing.Cubic.InOut, true, 0);

    this._outlineAlphaTw = this.game.add.tween(this._outline).to({ alpha: 0 }, 500, Phaser.Easing.Cubic.InOut, true, 0);

    this._padScaleTw.universal = true;

    this._padScaleTw.onComplete.add(() => {
      this._innerGlow.alpha = 0;
    });

    this._outlineAlphaTw.onComplete.add(() => {
      this._outline.scale.set(1, 1);
      this._outline.alpha = 1;
    });
  }

  _build() {
    this._buildOutline();
    this._buildBg();
    this._buildInnerGlow();
    this._buildBlocker();
  }

  _buildOutline() {
    const gr = this.game.add.graphics();
    gr.lineStyle(5, 0xffffff, 1);
    gr.drawRoundedRect(-70, -70, 140, 140, 10);
    gr.endFill();

    this.addChild((this._outline = gr));
  }

  _buildBg() {
    const { color } = store.game.boardModel.getPadByUuid(this._uuid);
    this._bg = makeImage(getPadImageConfig(color));
    this._bg.name = store.game.boardModel.getPadByUuid(this._uuid).sound;

    this.addChild(this._bg);
  }

  _buildInnerGlow() {
    this._innerGlow = makeImage(getPadInnerGlowImageConfig());
    this._innerGlow.alpha = 0;

    this.addChild(this._innerGlow);
  }

  _buildBlocker() {
    this._blocker = makeImage(getPadImageConfig('cube_black'));

    this.addChild(this._blocker);
  }

  _onClick() {
    this.click();
    this.onClick.dispatch(this._uuid);
  }
}
