import { getNextButtonIconImageConfig } from '../../configs/image-configs';
import { fitText, makeImage, makeNinePatch, makeText } from '../../utils';
import { AbstractButton } from '../../utils/button/abstract-button';

export class NextButton extends AbstractButton {
  constructor(config) {
    super(config);
    this.createIcon();
  }

  createIcon() {
    const icon = makeImage(getNextButtonIconImageConfig());
    // @ts-ignore
    icon.right = this.states.up.right - 30;
    this.addChild(icon);
  }

  createState(config) {
    const state = new Phaser.Group(this.game);

    const { bg, label, fitHeight = 0.9 } = config;

    // FRAME
    const bgObj = bg.width && bg.height ? makeNinePatch(bg) : makeImage(bg);
    state.add(bgObj);

    // LABEL
    if (label) {
      const labelObj = makeText(label);
      labelObj.anchor.set(0.5);
      state.add(labelObj);

      fitText(labelObj, bgObj.width * 0.45, bgObj.height * fitHeight);
    }

    return this.add(state);
  }
}
