/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getHandImageConfig } from '../../configs/image-configs';
import { ViewEvents } from '../../events/view-events';
import {
  delayRunnable,
  handMoveTw,
  handScaleTw,
  hideToRight,
  makeImage,
  removeRunnable,
  showFromRight
} from '../../utils';
import { last } from '../../utils/array/last';
import { Container } from '../../utils/container';
import { OptionComponent } from './option-component';

const optionSetCOnfig = [
  {
    icon: '1',
    title: 'Faded',
    singer: 'Alan Walker'
  },
  {
    icon: '2',
    title: 'Closer',
    singer: 'The Chainsmokers'
  }
];

export class OptionSetComponent extends Container {
  constructor() {
    super();

    this._optionsShowTweens = [];
    this._optionsHideTweens = [];
    this._options = [];

    this._build();
  }

  getBounds() {
    return new Phaser.Rectangle(-265, -90, 530, 400);
  }

  show() {
    this._showOptions();
    last(this._optionsShowTweens).onComplete.add(() => {
      this._showHand();
      this._options.forEach(o => o.enable());
    });
  }

  hide() {
    this.removeActions();
    this._hand.destroy();
    return this._hideOptions();
  }

  removeActions() {
    removeRunnable(this._highlightRunnable);
    removeRunnable(this._previewRunnable);
    this.game.tweens.remove(this._moveTw);
    this.game.tweens.remove(this._showTw);
    this.game.tweens.remove(this._alphaTw);
    this.game.tweens.remove(this._scaleTw);
    this.game.tweens.remove(this._scaleBackTw);
    this._optionsShowTweens.forEach(tw => {
      this.game.tweens.remove(tw);
    });
    this._optionsHideTweens.forEach(tw => {
      this.game.tweens.remove(tw);
    });
  }

  _build() {
    this._buildOptions();
    this._buildHand();
  }

  _buildOptions() {
    optionSetCOnfig.forEach((config, i) => {
      const option = new OptionComponent(config);
      this.addChild(option);
      this._options.push(option);
      option.alpha = 0;
      option.y = i * (option.height + 25);
    });
  }

  _buildHand() {
    const hand = makeImage(getHandImageConfig());
    hand.visible = false;
    this.addChild((this._hand = hand));
  }

  _showOptions() {
    this._optionsShowTweens = this._options.map((opt, i) => {
      opt.alpha = 1;
      return showFromRight(opt, 150 * i, 1000, false, Phaser.Easing.Back.Out);
    });
  }

  _hideOptions() {
    this._optionsHideTweens = this._options.map((opt, i) => {
      opt.alpha = 1;
      return hideToRight(opt, 150 * i, 1000, false, Phaser.Easing.Back.In);
    });

    return last(this._optionsHideTweens);
  }

  _showHand() {
    this._positions = this._getHintPositions();
    this._currentPoint = 0;

    this._firstTimeShow();
  }

  _firstTimeShow() {
    const point = this._positions[this._currentPoint];
    this._hand.scale.set(1);
    this._hand.alpha = 1;
    this._hand.position.set(point.x, point.y);
    this._hand.angle = 0;
    this._hand.visible = true;
    const { x, y, angle } = this._hand;

    this._showTw = this.game.add
      .tween(this._hand)
      .from({ x: x + 50, y: y + 50, angle: angle + 30 }, 400, Phaser.Easing.Cubic.Out, true, 0);
    this._showTw.universal = true;
    this._showTw.onComplete.addOnce(() => {
      this._pointHand();
    });

    this._alphaTw = this.game.add.tween(this._hand).from({ alpha: 0 }, 400, Phaser.Easing.Cubic.Out, true, 0);
    this._alphaTw.universal = true;
  }

  _pointHand() {
    this._scaleTw = handScaleTw(this._hand);
    this._scaleTw.universal = true;

    this._highlightRunnable = delayRunnable(200, () => {
      this._options[this._currentPoint].highlightGlow();
    });

    this._scaleTw.onComplete.addOnce(() => {
      this._options[this._currentPoint].play();
      lego.event.emit(ViewEvents.CtaView.OptionPreview, this._currentPoint);

      this._previewRunnable = delayRunnable(4500, () => {
        this._scaleBackTw = handScaleTw(this._hand);
        this._scaleBackTw.universal = true;

        this._scaleBackTw.onComplete.addOnce(() => {
          this._options[this._currentPoint].pause();

          this._currentPoint += 1;
          if (this._currentPoint === this._positions.length) {
            this._currentPoint = 0;
          }

          this._moveHand(this._positions[this._currentPoint]);
        });
      });
    });
  }

  _moveHand(pos) {
    this._moveTw = handMoveTw(this._hand, pos);
    this._moveTw.universal = true;
    this._moveTw.onComplete.addOnce(() => {
      this._pointHand();
    });
  }

  _getHintPositions() {
    return this._options.map(opt => {
      return this.toLocal(opt.toGlobal(opt.playButton.position), undefined);
    });
  }
}
