import { getProgressBarDotGlowImageConfig, getProgressBarDotImageConfig } from '../../configs/image-configs';
import { makeImage, postRunnable } from '../../utils';
import { Container } from '../../utils/container';

export class DotComponent extends Container {
  constructor(uuid) {
    super();

    this._uuid = uuid;

    this._build();
  }

  get uuid() {
    return this._uuid;
  }

  getBounds() {
    return this._dot.getBounds();
  }

  idle() {
    this._fill.alpha = 0;
    this._glow.alpha = 0;
    this.game.tweens.remove(this._alphaTw);
  }

  glow() {
    postRunnable(() => {
      this._alphaTw = this.game.add
        .tween(this._glow)
        .to({ alpha: 1 }, 1000, Phaser.Easing.Sinusoidal.InOut, true, 0, -1, true);
      this._alphaTw.universal = true;
    });
  }

  fill() {
    this._fill.alpha = 1;
  }

  _build() {
    this._buildGlow();
    this._buildDot();
    this._buildFill();
  }

  _buildGlow() {
    this._glow = makeImage(getProgressBarDotGlowImageConfig());

    this.addChild(this._glow);
  }

  _buildDot() {
    this._dot = makeImage(getProgressBarDotImageConfig());

    this.addChild(this._dot);
  }

  _buildFill() {
    const gr = this.game.add.graphics();
    gr.beginFill(0xfef955, 1);
    gr.drawCircle(0, 0, 25);
    gr.endFill();

    this.addChild((this._fill = gr));
  }
}
