/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getCtaPlayButtonConfig, getCtaRetryButtonConfig } from '../../configs/button-configs';
import { InputPriority } from '../../constants';
import { ViewEvents } from '../../events/view-events';
import moduleBindings from '../../module-bindings';
import { makePixel } from '../../utils';
import { Button } from '../../utils/button/button';
import { CtaLogoView } from './cta-logo-view';

export class CTAView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._blocker = null;
    this.bindings = moduleBindings;
  }

  get name() {
    return 'CTAView';
  }

  get logoPosition() {
    return this.toGlobal(this._logo.position);
  }

  getGridConfig() {
    return new Error('function getGridConfig() is not implemented');
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  buildLogo() {
    this._logo = new CtaLogoView();
    this._logo.alpha = 0;
    this.setChild('logo', this._logo);
  }

  buildPlayButton(cellName = 'play_button') {
    this._playBtn = new Button(getCtaPlayButtonConfig(CI_API.Globals.PARAMS.cta_btn_text));
    this._playBtn.alpha = 0;

    this.setChild(cellName, this._playBtn);
  }

  buildRetryButton() {
    this._retryBtn = new Button(getCtaRetryButtonConfig());
    this._retryBtn.alpha = 0;

    this.setChild('retry_button', this._retryBtn);
  }

  build(config, blockerAlpha = 0) {
    super.build(config);
    this._buildBlocker(blockerAlpha);
  }

  showBlocker() {
    this._showBlockerTw = this.game.add
      .tween(this._blocker)
      .to({ alpha: 0.8 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0);
    this._showBlockerTw.universal = true;

    return this._showBlockerTw;
  }

  hideBlocker() {
    this._hideBlockerTw = this.game.add
      .tween(this._blocker)
      .to({ alpha: 0 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0);
    this._hideBlockerTw.universal = true;

    return this._hideBlockerTw;
  }

  _buildBlocker(alpha) {
    this._blocker = makePixel({ alpha });
    this._blocker.inputEnabled = true;
    this._blocker.input.priorityID = InputPriority.Cta;
    this._blocker.name = 'cta_overlay';
    this.setChild('blocker', this._blocker);
  }

  _buildButton(config, callback) {
    const btn = new Button(config);
    btn.onClick.add(callback, this);
    return btn;
  }

  _onScreenClick() {
    if (!this._hideBlockerTw) {
      lego.event.emit(ViewEvents.CtaView.ScreenClick);
    }
  }

  _onPlayClick() {
    lego.event.emit(ViewEvents.CtaView.PlayClick);
  }

  _onRetryClick() {
    lego.event.emit(ViewEvents.CtaView.RetryClick);
    this._retryBtn.onClick.remove(this._onRetryClick, this);
  }
}
