import { FONT1, FONT2 } from '../constants';

// TUTORIAL
export function getTutorialTextConfig(text, fill) {
  return {
    text,
    style: {
      font: FONT1,
      fontSize: 48,
      fill,
      align: 'center',
      wordWrap: true,
      wordWrapWidth: 400,
      stroke: '#000000',
      strokeThickness: 5
    }
  };
}

