export function getCTAStandardLabelTextConfig() {
  return {
    text: {
      text: ` ${localization.get('WELL DONE!')} `,
      ignoreLocalization: true,
      toString: function toString() {
        return this;
      }
    },
    style: {
      font: FONT1,
      fontSize: 85,
      fill: '#ffffff',
      shadow: {
        x: 0,
        y: 0,
        color: '#5fbfd0',
        blur: 25,
        shadowStroke: true,
        shadowFill: true
      }
    }
  };
}

export function getCTAEmbeddedProgressTextConfig(progress) {
  const percent = progress * 100;
  return {
    text: {
      text: `  ${percent.toFixed(0)}%  `,
      ignoreLocalization: true,
      toString: function toString() {
        return this;
      }
    },
    style: {
      font: FONT1,
      fontSize: 90,
      fill: '#fef955',
      shadow: {
        x: 0,
        y: 0,
        color: '#fef955',
        blur: 20,
        shadowStroke: true,
        shadowFill: true
      }
    }
  };
}
